import React from "react";
import styled from "react-emotion";
import { Link } from "react-router-dom";
import { ImageData } from "../components";
import { Colors } from "../config";

interface WrapperProps {
  reverse?: boolean;
}

const Wrapper = styled<WrapperProps, "div">("div")`
  display: flex;
  flex-flow: ${p => (p.reverse ? `row-reverse` : "row")} nowrap;
  max-width: 1024px;
  margin: 0 0 auto;
`;

interface ColumnProps {
  isReverseText?: boolean;
}

const Column = styled<ColumnProps, "div">("div")`
  flex: 1 1 50%;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  ${p =>
    p.isReverseText &&
    `
    align-items: flex-end;
  `};
`;

interface ImageProps {
  border?: string;
}

const Image = styled<ImageProps, "img">("img")`
  width: 100%;
  ${p => p.border && `border: 5px solid ${p.border};`};
`;

interface HeadingProps {
  color?: string;
}

const Heading = styled<HeadingProps, "h2">("h2")`
  color: ${p => p.color || p.theme.colors.text};
  font-size: 2.6rem;
  margin: 1rem 0 0.5rem 0;
`;

const SubHeading = styled("h3")`
  margin: 0.5rem 0;
  font-size: 2rem;
  font-weight: 100;
`;

interface GaleryButtonProps {
  color?: Colors;
}
const GaleryLink = styled<GaleryButtonProps, "span">("span")`
  margin: 2rem 0;
  ${p => p.theme.textShadow};
  a {
    text-decoration: none;
    &:link, &: visited {
      color: ${p => (p.color ? p.color : p.theme.colors.primary)};
    }
    &:hover, &:active {
      color: ${p => p.theme.colors.text}
      text-decoration: underline;
    }
  }
`;

const Content = styled("p")``;

interface Props {
  imageData: ImageData;
  heading: string;
  subHeading: string;
  imageAlign?: "left" | "right";
  galeryRoute?: string;
  color?: Colors;
}

const ArtistPreview: React.SFC<Props> = ({
  imageData: { url, ...imageData },
  heading,
  subHeading,
  color,
  imageAlign,
  children,
  galeryRoute
}) => (
  <Wrapper reverse={imageAlign && imageAlign === "right"}>
    <Column>
      <Image src={url} {...imageData} />
    </Column>
    <Column isReverseText={imageAlign && imageAlign === "right"}>
      <Heading color={color}>{heading}</Heading>
      <SubHeading>{subHeading}</SubHeading>
      {galeryRoute && (
        <GaleryLink color={color}>
          <Link to={galeryRoute}>Weiter zur Galerie</Link>
        </GaleryLink>
      )}
      <Content>{children}</Content>
    </Column>
  </Wrapper>
);

export default ArtistPreview;



